@import url('https://fonts.googleapis.com/css2?family=Muli&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css');
html {
  font-size: 72%;
}
*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;
  font-family: 'Muli',sans-serif;
}

.navbar {
  background-color: rgb(226, 35, 35) !important;
  /* margin-left: 40px; */
  /* margin-right: 40px; */
}

.navbar-brand {
  font-size:2.4rem !important;
  color: #5652a1 !important;
}

.navbar a {
  font-size:2.1rem;
  text-transform: capitalize;
}
.navbar a:hover{
  color:blue;
}

.menu_active {
  font-weight: bold;
  border-bottom: .1rem solid #565387;
}
.nav-link:hover {
color: rgb(10, 160, 85);
}
.brand-name {
  color: orange;
}

#header {
  width:100%;
  height: 80vh;
  margin-top: 700;
}
#header h1 {
  margin: 0 0 1rem 0;
  font-size: 5.8rem;
  font-weight: 700;
  color: black;
}
/* #header .brand-name {
  color: skyblue;
} */

#header .btn-get-started {
  font-weight: 500;
  font-size: 1.5rem;
  letter-spacing:1rem;
  display: inline-block;
  padding: .8rem 1.5rem;
  border-radius: 5.0rem;
  transition: 0.5s;
  color: orange;
  border: .2rem solid rgb(9, 11, 156);
  text-decoration: none;
  /* background-colo; */
}
.btn-get-started:hover {
  background-color: skyblue;
  color: green;
}

.logo {
  height: 5.0rem;
  width: 5.0rem;
  border: .2rem solid white;
  border-radius: 1.0rem;
  background-color: red;
}

.image {
  font-size: 1.8rem;
}
.text-center {
  font-size: 2.8rem;
}
.form-control {
  width: 20rem;
}
.copyright {
  font-size: 1.5rem;
}
.form-control {
  height: 2.3rem;
}
@media(max-width:998px) {
  html {
    font-size: 55%;
  }
}
@media(max-width:768px) {
  html {
    font-size: 45%;
  }
}
